html, body {
    overflow-x: hidden;
    
    }
body {
    font-family: "Noto Sans", "Roboto", sans-serif;
}
ul,
li {
    list-style: none;
    margin: 0;
    padding: 0;
}
a {
    text-decoration: none;
    display: block;
}
img {
    width: 100%;
    object-fit: cover;
    object-position: center;
}
h1,h2,h3,h4,h5,p{
    margin-bottom: 0;
}

